import classes from './Bio.module.scss';
import Title from '../UI/Title';
import { useEffect, useRef } from 'react';
import { gsap } from 'gsap';
import BrandsMarquee from '../UI/brands-marquee/BrandsMarquee';
import StarIcon from '@mui/icons-material/Star';

const Bio = () => {
    const headline = useRef();
    const subline = useRef();
    const boldFont = useRef();

    useEffect(() => {
        gsap.to([headline.current, subline.current], {
            // repeat: -1,
            // yoyo: true,
            color: '#705475',
            duration: 5,
            stagger: 0.3,
        });
    }, []);
    return (
        <div className={classes.bio__wrapper}>
            <div className={classes.title}>
                <Title titleBackgroundClass="title-background__bio" titleText="Bio" />
            </div>
            <div className={classes.header}>
                <h2 ref={headline}>
                    Web developer with over{' '}
                    <span className={classes.bolder} ref={boldFont}>
                        10 years
                    </span>{' '}
                    professional experience.
                </h2>
                <h3 ref={subline}>Coding &amp; design with UX at its heart.</h3>
            </div>
            <div className={`${classes.para1} ${classes['bio-text']}`}>
                <h3>Projects</h3>
                <p>
                    I have worked prominently in the advertisting industry on many brands including:
                </p>
                <BrandsMarquee />
            </div>
            <div className={`${classes.para2} ${classes['bio-text']}`}>
                <h3>Professional experience</h3>
                <ul>
                    <li>
                        <StarIcon /> Working 10 years in the advertisting sector, designing &amp;
                        building rich media banners. From simple in-page units to multi-media
                        expandables, full-width responsive and takeovers. Talking directly to client
                        to create and amend ad units to their specification. Ensuring the approved
                        ad unit is compatiable for the publisher.
                    </li>
                    <li>
                        <StarIcon /> Creative agency - Object orientated Actionscript Flash apps.
                    </li>
                    <li>
                        <StarIcon /> Travel technology - maintaining a flight search engine
                        (PHP/MySQL), early days HTML4/ASP webpages and lead the tech support team.
                    </li>
                    <li>
                        <StarIcon /> Also worked on simple websites for local businesses, administer
                        our local Foodbank website &amp; email system and HTML emails for residentâ€™s
                        society.
                    </li>
                    <li>
                        <StarIcon /> Last 18 months, learning React, Vue, Firebase, NodeJS/Express
                        and MongoDB.
                    </li>
                </ul>
            </div>
        </div>
    );
};

export default Bio;
